.social__list {
  @include clear-list;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.social__item {
  margin-right: 6px;

  &:last-child {
    margin-right: 0;
  }
}

.social__link {
  background-color: $style;
  display: inline-block;
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  padding-top: 17px;
}
